// font-family: 'Montserrat', sans-serif;
// font-family: 'Playfair Display', serif;

html {
	box-sizing: border-box;
	font-size: 100%;
}

*,
*:before,
*:after {
	box-sizing: inherit;
	margin: 0;
	padding: 0;
}

:root {
	--orange: #f2994a;
}

body {
	font-family: "Montserrat", sans-serif;
	padding: 32px 12px;
  min-width: 350px;
  max-width: 970px;
  margin: 0 auto;
}

header {
	margin-bottom: 24px;
	h1 {
		font-family: "Playfair Display", Georgia, "Times New Roman", Times, serif;
		font-style: normal;
		font-weight: bold;
		font-size: 24px;
		line-height: 32px;
		color: #000000;
	}
	p {
		font-style: italic;
		font-weight: 500;
		font-size: 12px;
		line-height: 15px;
		color: #4f4f4f;
		margin: 12px 24px;
		position: relative;
	}
	p:before {
		content: "";
		position: absolute;
		background-image: url("./6dots.svg");
		left: -24px;
		top: 4px;
		width: 13px;
		height: 8px;
	}
	figure img {
		border-radius: 12px;
		margin-top: 12px;
	}
}

aside {
	h6 {
		font-weight: bold;
		font-size: 8px;
		line-height: 10px;
		text-transform: uppercase;
		color: #bdbdbd;
	}
	p {
		font-weight: 500;
		font-size: 10px;
		line-height: 12px;
		color: #333333;
	}
	.material-icons {
		font-size: 15px;
		color: #4f4f4f;
		margin-right: 6px;
	}
	.yields i,
	.yields p {
		color: var(--orange);
	}

	.group {
		display: flex;
		align-items: center;
		margin-bottom: 24px;
	}
	.time {
		display: flex;
		justify-content: space-between;
	}
}

main {
	h2 {
		font-family: Playfair Display;
		font-style: normal;
		font-weight: bold;
		font-size: 18px;
		line-height: 24px;
		color: #333333;
		margin-bottom: 12px;
	}
	h3 {
		font-family: Playfair Display;
		font-style: italic;
		font-weight: normal;
		font-size: 16px;
		line-height: 21px;
		color: #333333;
		margin-bottom: 24px;
	}
	label {
		font-size: 14px;
		line-height: 25px;
		color: #333333;
	}
	.cb-container {
		display: block;
		position: relative;
		padding-left: 35px;
		margin-bottom: 3px;
		cursor: pointer;
	}
	.cb-container input {
		position: absolute;
		opacity: 0;
		cursor: pointer;
		height: 0;
		width: 0;
	}
	.checkmark {
		position: absolute;
		top: 1px;
		left: 0;
		height: 21px;
		width: 21px;
		border: 1px solid #828282, 100%;
		border-radius: 4px;
	}
	.cb-container:hover input ~ .checkmark {
		background-color: #f5d4b6;
	}
	.cb-container input:checked ~ .checkmark {
		background-color: var(--orange);
	}
	ul {
    margin-bottom: 36px;
    list-style-type: none;
	}
}

.instructions {
	ol {
		margin-left: 51px;
		counter-reset: my-counter;
		list-style: none;
	}
	li {
		font-weight: normal;
		font-size: 16px;
		line-height: 1.5;
		color: #333333;
		counter-increment: my-counter;
		position: relative;
		margin-bottom: 24px;
	}
	li::before {
		content: counter(my-counter);
		display: block;
		width: 36px;
		height: 36px;
		background-color: var(--orange);
		position: absolute;
		text-align: center;
		left: -51px;
		font-family: "Playfair Display";
		font-weight: bold;
		font-size: 24px;
		line-height: 1.2;
		color: #ffffff;
		border-radius: 6px;
	}
}

.source {
	font-family: Montserrat;
	font-style: italic;
	font-weight: normal;
	font-size: 12px;
	line-height: 150%;
	color: #bdbdbd;
}

footer {
	font-family: Montserrat;
	font-style: normal;
	font-weight: 600;
	font-size: 14px;
	line-height: 17px;
  color: #a9a9a9;
  margin-top: 126px;
  text-align: center;
}
